@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h2 {
    @apply text-4xl font-poppins_bold my-3;
  }
}

@layer base {
  h3 {
    @apply text-3xl font-poppins_bold my-3;
  }
  h4 {
    @apply text-lg font-medium;
  }
  p {
    @apply text-sm font-poppins_regular;
  }
  span {
    @apply text-base font-poppins_regular;
  }
  a {
    @apply font-poppins_regular;
  }
}
/*/////////////////////////////////// font family//////////////////////// */
@font-face {
  font-family: poppins_bold;
  src: url("../components/font/Poppins-Bold.ttf");
}
@font-face {
  font-family: poppins_regular;
  src: url("../components/font/Poppins-Regular.ttf");
}
@layer utilities {
  .font-poppins_bold {
    font-family: "poppins_bold", sans-serif;
  }
  .font-poppins_regular {
    font-family: "poppins_regular", sans-serif;
  }
  .bg-loginIntroduce {
    background-image: linear-gradient(
      90deg,
      rgb(63, 119, 209) 25%,
      rgb(80, 141, 239)
    );
  }
  .shadow-decorationblock {
    box-shadow: 0 0 0 60px hsl(0deg 0% 100% / 10%);
  }
}
/* /////login page//////////////////////////////////////////////////// */
@layer components {
  /* /////login */
  /* */
  .wrap {
    @apply m-0 p-0 h-auto flex flex-col;
  }
  .wrap-sm {
    @apply m-12  min-h-[600px] flex-col h-auto;
  }
  .wrap-md {
    @apply m-12  min-h-[600px] flex-row h-auto;
  }
  .wrap-lg {
    @apply m-24 h-[calc(100vh-200px)] min-h-[600px];
  }

  /* /////login aside left */
  .introduce {
    @apply p-12 w-full flex flex-col justify-center items-center bg-loginIntroduce text-center rounded-t-xl rounded-l-none overflow-hidden relative;
  }
  .introduce-sm {
    @apply p-12 w-full flex flex-col justify-center items-center bg-loginIntroduce text-center rounded-l-none rounded-t-xl  overflow-hidden;
  }
  .introduce-md {
    @apply p-12 w-3/5 bg-loginIntroduce relative overflow-hidden rounded-t-none rounded-l-xl flex flex-col justify-between items-start text-left;
  }

  .introduce-logo {
    @apply flex items-center;
  }

  /* ////login aside right */
  .form {
    @apply w-full p-12 bg-white flex flex-col justify-center rounded-r-none rounded-b-xl;
  }
  .form-md {
    @apply w-2/5  rounded-b-none rounded-r-xl;
  }
}

/* /////////////////signup/////////////////////////////////////////// */
@layer components {
}

.animate-balloons1 {
  animation: balloons 5s linear infinite;
}
.animate-balloons2 {
  animation: balloons 8s linear infinite;
}

@keyframes balloons {
  0% {
    transform: rotate(45deg);
  }
  25% {
    transform: rotate(45deg) translate(5px, -10px);
  }
  50% {
    transform: rotate(45deg);
  }
  75% {
    transform: rotate(45deg) translate(5px, 10px);
  }
  100% {
    transform: rotate(45deg);
  }
} ;
